import React, { useEffect } from 'react';

/**
 * FontLoadingOptimizer component
 * Optimizes font loading by detecting when fonts are loaded and applying appropriate classes
 * Prevents FOIT (Flash of Invisible Text) and FOUT (Flash of Unstyled Text)
 * Enhanced with better performance monitoring and progressive loading
 */
const FontLoadingOptimizer = () => {
  useEffect(() => {
    // Performance mark for font loading start
    if (typeof performance !== 'undefined' && performance.mark) {
      performance.mark('font-loading-start');
    }

    // Check if Font Loading API is supported
    if ('fonts' in document) {
      // Add font-loading class initially
      document.documentElement.classList.add('font-loading');

      // Load critical fonts with optimized strategy
      const loadFonts = async () => {
        try {
          // Load the most critical font weights first (400 is most common)
          await document.fonts.load('400 1em Fredoka');

          // Mark critical font loaded
          if (typeof performance !== 'undefined' && performance.mark) {
            performance.mark('critical-font-loaded');
          }

          // Load secondary critical weight (500 for headings)
          await document.fonts.load('500 1em Fredoka');

          // Remove loading class and add loaded class
          document.documentElement.classList.remove('font-loading');
          document.documentElement.classList.add('font-loaded');

          // Performance mark for font loading complete
          if (typeof performance !== 'undefined' && performance.mark) {
            performance.mark('font-loading-complete');
            performance.measure('font-loading-duration', 'font-loading-start', 'font-loading-complete');
          }

          // Preload additional weights in the background with lower priority
          const loadAdditionalFonts = () => {
            Promise.all([
              document.fonts.load('300 1em Fredoka'),
              document.fonts.load('600 1em Fredoka'),
              document.fonts.load('700 1em Fredoka'),
            ]).catch(error => {
              console.warn('Additional font weights failed to load:', error);
            });
          };

          // Use requestIdleCallback if available, otherwise setTimeout
          if (typeof requestIdleCallback !== 'undefined') {
            requestIdleCallback(loadAdditionalFonts, { timeout: 2000 });
          } else {
            setTimeout(loadAdditionalFonts, 100);
          }

        } catch (error) {
          console.warn('Font loading failed, using fallbacks:', error);
          // Still remove loading class to show fallback fonts
          document.documentElement.classList.remove('font-loading');
          document.documentElement.classList.add('font-fallback');

          // Performance mark for fallback
          if (typeof performance !== 'undefined' && performance.mark) {
            performance.mark('font-fallback-activated');
          }
        }
      };

      loadFonts();

      // Set a timeout fallback in case font loading takes too long (reduced to 2s for better UX)
      const fallbackTimeout = setTimeout(() => {
        if (document.documentElement.classList.contains('font-loading')) {
          document.documentElement.classList.remove('font-loading');
          document.documentElement.classList.add('font-timeout');

          // Performance mark for timeout
          if (typeof performance !== 'undefined' && performance.mark) {
            performance.mark('font-loading-timeout');
          }
        }
      }, 2000); // 2 second timeout for better perceived performance

      return () => {
        clearTimeout(fallbackTimeout);
      };
    } else {
      // Fallback for browsers without Font Loading API
      document.documentElement.classList.add('font-no-api');

      // Performance mark for no API fallback
      if (typeof performance !== 'undefined' && performance.mark) {
        performance.mark('font-no-api-fallback');
      }
    }
  }, []);

  return null; // This component doesn't render anything
};

export default FontLoadingOptimizer;
